/* Global Properties */
html, body { padding:0; margin:0; height:100%;}

body {
	font: 13px/19px $title-font, Arial, Helvetica, sans-serif;
	background: #232322;
	min-width:300px;
}

.fs_14{font-size: 14px;}

#back-top {
  position: fixed;
  left: 51%;
  margin-left: 500px;
  bottom: 40px;
  z-index: 1000;
  display: none;
  width: 22px;
  height: 22px;
  background: url("../images/up-arrow.png") 0 center no-repeat;
  cursor: pointer;
}

#back-top:hover {
  background: url("../images/up-arrow.png") 100% center no-repeat;
}

.main{
	width:100%;
	height: auto;
	padding:0 0 0 0; 
}

.div-content{
	width:100%;
	margin:0 auto;
	background: #eeeded;
	padding-top: 0px;
	
}

.txt_col1{
	position: relative;
	display: inline-block;
	width: 64px;
}

h2{
	margin: 0;
	font:600 20px/20px $title-font, Arial, sans-serif;
	color:#fff;
	text-transform: uppercase;
	margin-top: 36px;
}

h2.v2{color:#232322;}

h3{
	margin: 0;
	font: 600 17px/17px $title-font, Arial, sans-serif;
	color:#c83939;
	margin-top: 19px;
	text-transform: uppercase;
}

h3.objetives {
	color: orange;
	margin: 0 0 0.25em 0;
}

h3.v2{margin-top: 25px;}

h3.v3{margin-top: 25px;}

h3.v4{margin-top: 25px; font: 600 13px/13px $title-font, Arial, sans-serif;}

h4{
	font:300 18px/20px $title-font, Arial, sans-serif;
	color:#aaaaaa; 
	margin-top: 19px;
}

h5{
	font:14px/22px $title-font, Arial, sans-serif;
	color:#333333; 
	margin-top: 10px;
	margin-bottom: 0;
	text-transform: uppercase;
}

.txt1{
	margin: 0;
	font:600 13px/20px $title-font, Arial, sans-serif;
	color:#c83939;
	text-transform: uppercase;
}

.banner1 i {
	float: left;
	position: relative;
	left: 2px;
	top: 25px;
	font-weight: normal;
	margin-left: 1px;
	font-size: 57px;
	line-height: 1em;
	font-family: 'FontAwesome';
	color: #595959;
	width: 58px;
	min-height: 55px;
}

.banner_content .banner1 i {
	top: 4px;
	font-size: 46px;
	left: 0;
}

.banner1 p{margin-top: 0;}

i.icon-thumbs-up {
	font-size: 54px;
	top: 3px;
}

i.icon-signal {
	font-size: 44px;
	top: 23px;
}

i.icon-ok {
	font-size: 52px;
	top: 20px;
}

.banner1 .txt1, .banner1 i, .banner2 h3, .banner2{
	-webkit-transition: all 0.6s ease;
  -moz-transition: all 0.6s ease;
  -o-transition: all 0.6s ease;
  transition: all 0.6s ease;
}

.banner1:hover .txt1 a{color: #fff;}

.banner1:hover i { color: orange; }

.banner2{
	background: #3d3d3d;display: inline-block;width: 100%;
	box-sizing: border-box;
	-moz-box-sizing: border-box; /*Firefox 1-3*/
	-webkit-box-sizing: border-box; /* Safari */
	cursor: pointer;
	margin-top: 10px;
	padding-right: 10px;
}


.banner2 p{margin-top: 1px; color: #fff;}
.banner2:hover{background: #F18A00;}
.banner2:hover h3 a{color: #fff;}

.box-1 {
	width: 100%;
	background: #232322;
	padding-bottom: 50px;
	margin-top: 20px;
}
.box-1.v2{margin-top: 23px;}
.box-1.v3{margin-top: 1px;}

.figure_video{margin-top: 75px; display: block;}

.figure_video a{display: block;}

.box-1 h2 {
	margin-top: 20px;
}

.box-1 .about-us {
	margin-right: 1em;
}

.box-1 .about-us p {
	font-size: 1.15em;
  margin-top: 1.5em;
}

.b_right{
	border-right: 1px solid #c0c0c0;
	box-sizing: border-box;
	-moz-box-sizing: border-box; /*Firefox 1-3*/
	-webkit-box-sizing: border-box; /* Safari */
}

.fw_video {
  margin-top: 75px; display: block;
  width: 100%;
  max-width: 570px;
  min-width: 290px;
  height: 323px;
  overflow: hidden;
  border: 5px solid #2a2a2a;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.fw_video iframe {
  position: relative;
  display: inline-block;
  outline: none;
  width: 100%;
  height: 100%;
  border: none;
}

.lightbox-video{
	position:relative;
	display:inline-block;
}

.lightbox-video span {
	display:block;
	position:absolute;
	left:0;
	top:0;
	width:100%;
	height:100%;
	background: url(../images/magnify-video.png) no-repeat 50% 50%;
	z-index:1;
}

.white_bg{
	width: 100%;
	position: relative;
	background: #fff;
	border: none;
	margin-top: 40px;
	margin-bottom: 1px;
	padding: 29px 0 13px;
}

.date_section{
	font: 600 16px/14px $title-font, Arial, sans-serif;
	color:#fff; 
	display: block;
	float: left;
	width: 60px;
	height: 61px;
	padding-top: 15px;
	background: #e84a3a;
	text-align: center;
	margin-right: 18px;
	box-sizing: border-box;
	-moz-box-sizing: border-box; /*Firefox 1-3*/
	-webkit-box-sizing: border-box; /* Safari */

	-webkit-transition: all 0.6s ease;
  -moz-transition: all 0.6s ease;
  -o-transition: all 0.6s ease;
  transition: all 0.6s ease;
}

.comment_link{
	margin-right: 31px;
	margin-top: 31px;
}

.main_date_section:hover .date_section{background: #e74c3c;}

p{margin-top: 24px;}

img{max-width:100%; height:auto; margin-bottom: 0px;}

figure{position: relative;}

figure span.img_txt{
	background-color: rgba(241, 138, 0, 0.75);
	background: #f18a00;
	bottom: -1px;
	color: #fff;
	font:600 27px/27px $title-font, Arial, sans-serif;
	left: 0;
	padding: 11px 20px 12px;
	position: absolute;
	text-transform: uppercase;
	z-index: 100;
}

.img1{margin-top: 40px;}

.img2{
	margin-top: 0px;
	float: left;
	margin-right: 20px;
}

.img3{
	margin-top: 28px;
	float: left;
	margin-right: 10px;
}

.img4{margin-top: 28px;}

.img5{margin-top: 28px;}


a {
	text-decoration:none; 
	cursor:pointer; 
	color: #323435;
}
a.link1{color: orange;}

a.link1:hover{color: #fff;}

a.link2{color: #7f7f7f;}

a.link2:hover{color: #fff;}

a.link3{color: #c83939;}

a.link3:hover{color: #000;}

a.link4{color: #232322; text-decoration: underline;}

a.link4:hover{
	color: #c83939;
	text-decoration: underline;
}

a:hover{
	text-decoration:none;
	color: #000;
}

a{
	-webkit-transition:all 0.3s ease;
	-moz-transition:all 0.3s ease;
	-o-transition:all 0.3s ease;
	transition:all 0.3s ease;
}

.footer_section{
	width: 100%;
	padding-top: 1px;
	padding-bottom: 25px;
	background: #F18A00;
}

.footer_section br{display: none;}

.footer_section p{
	margin-top: 22px;
	color: #fff;
	font:600 14px/20px $title-font, Arial, sans-serif;	
	margin-bottom: 0px;
	text-transform: uppercase;
}

.footer_section strong {
  position: relative;
  left: 0;
  top: 13px;
  font-weight: normal;
  margin: 0 15px 0 1px;
  font-size: 40px;
  line-height: 1em;
  font-family: 'FontAwesome';
  color: #fff;
  float: left;
}

.footer_section strong.icon-phone {margin-left: -5px;margin-right: 13px;}
