html, body {}

a[href^="tel:"] {
	color: inherit;
	text-decoration:none;
}
* {
	-webkit-text-size-adjust: none;
}
.clearfix {
  *zoom: 1;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
  line-height: 0;
}
.clearfix:after {
  clear: both;
}
.fleft {float:left;}
.fright {float:right;}
.clear {clear:both; line-height:0; font-size:0; width:100%;}
.wrapper, .wrapper-extra, .extra-wrap{
	position: relative;
	width: 100%;
	overflow:hidden; 
}
.wrapper-after:after{
	clear:both;
	content:".";
	display:block;
	height:0;
	visibility:hidden;
}
.dis-block{display: block;}
.dis-inblock{display: inline-block;}

.txt_left{text-align: left;}
.txt_right{text-align: right;}
.txt_cntr{text-align: center;}
.txt_ital{font-style: italic;}
.txt_line_through{text-decoration: line-through;}


.marg-left-0{margin-left:0 !important;}	
.marg-right-0{margin-right:0 !important;}
.marg-left-right-0{margin-left:0 !important; margin-right:0 !important;}
.last-mbot{margin-bottom:0 !important;}	
.last-pbot{padding-bottom: 0 !important;}
.last-mpbot{margin-bottom:0 !important;padding-bottom:0 !important;}

.marRight1{margin-right: 33px;}
.marRight2{margin-right: 55px;}

.padTop1{padding-top: 20px;}

.padBot1{padding-bottom: 60px;}
.padBot2{padding-bottom: 41px;}

.marTop1{margin-top: 10px;}
.marTop2{margin-top: 28px !important;}
.marTop3{margin-top: 20px !important;}

.marBot1{margin-bottom: 8px; display: inline-block;}
.marBot2{margin-bottom: 18px;}
.marBot3{margin-bottom: 29px;}

.hline2{
	margin-top: 22px;
	padding-bottom: 16px;
	border-bottom: 1px solid #393939;
	margin-bottom: -8px;
}
.hline3{
	margin-top: 15px;
	padding-bottom: 16px;
	border-bottom: 1px solid #c1c1c1;
}
.hline3 p{margin-top: 0px;}
.hline3.v2{border: none;}
.num{
	float: left;
	margin-right: 10px;
	background: #c83939;
	width: 40px;
	height: 40px;
	border-radius: 500px;
	color: #fff;
	text-align: center;
	font: 600 20px/40px $title-font, Arial, sans-serif;

	margin-top: 6px;
}

#page4 p{margin-bottom: 9px;}

.box, .box2, .box3{overflow: hidden;}
.bold{font-weight: bold;}

.color1{color: orange;}
.color2{color: #7f7f7f;}
.color3{color: #232322;}
.color4{color: #fff;}