/* lists */
.listWithMarker {
  margin: 0;
  margin-top: 25px;
}

.listWithMarker.v2{padding-top: 10px;}

.listWithMarker li {
  display: block;
  margin-bottom: 9px;
  padding-left: 12px;
  background: url(../images/list_marker.png) 0 5px no-repeat;
}

.listWithMarker li a {
  color: #7f7f7f;
  font:13px/16px $title-font, Arial, sans-serif;
  text-decoration: none;

}

.listWithMarker li a:hover {color: #fff;}

.listWithMarker.v2 li{
	padding-left: 12px;
	background: url(../images/list_marker.png) 0 11px no-repeat;
	margin-bottom: -1px;
}
.listWithMarker.v2 li a {
  color: #232322; 
  font:13px/29px $title-font, Arial, sans-serif;
}

.listWithMarker.v2 li a:hover {color: #c83939;}

.listWithMarker.v2{
  margin-top: 9px;margin-bottom: 38px;
}

.comments_btn{
  background: url(../images/comment_icon.png) 0 2px no-repeat;
  padding-left: 19px;
  color: #6b6b6b;
}

.more_arr_btn{
  position: relative;
  left: 0;
  top: 1px;
  font-weight: normal;
  margin-left: 1px;
  font-size: 18px;
  line-height: 1em;
  font-family: 'FontAwesome';
  color: orange;
  margin-right: 2px;
  padding-right: 2px;
}

.more_arr_btn:hover{
  color: #232322;
}

.more_btn {
  display: inline-block;
  text-decoration:none;
  font:600 13px/20px $title-font, Arial, sans-serif;
  color: #fff;
  padding: 8px 15px;
  /* padding-right: 22px; */
  background: #3d3d3d;
  margin-top: 25px;
  text-transform: uppercase;
}
.more_btn.v2{
  margin-top: 38px;
}

.more_btn:hover {
  color: #222;
  background: orange;
}

.icon-arrow-right{
  position: relative;
  left: 0;
  top: -1px;
  font-weight: normal;
  margin-left: 10px;
  font-size: 14px;
  line-height: 1em;
  font-family: 'FontAwesome';
}

figure {
  display: block;
}

figure.img_inner {
  display: block;
  height: auto;
  line-height: 0;
  overflow: hidden;
  padding-top: 28px;
  margin-bottom: 15px;
  float: left;
  margin-right: 9px;
}

figure iframe {
  width: 390px;
  height: 371px;
}

address {
  font-style: normal;
  display: block;
  margin-bottom: 20px;
  color: #232322;
  margin-top: 4px;
}

address dl dt {
  margin-bottom: 0px;
}

address dd span {
  position: relative;
  display: inline-block;
  width: 77px;
}

address a {
  color: #0bc0d3;
  text-decoration: none;
}

address a:hover {
  color: #f9129b;
}

/* Magnifier2 */
.magnifier2 {
    position:relative;
    display: inline-block;
}

.magnifier2 img {
  border: 2px solid #fff;
  -moz-box-shadow: 0 0 10px rgba(0,0,0,0.2);
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.2);
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
}

.zoom{
  position: absolute;
  width: 100%;
  height: 100%;
  border: 18px solid #2b91ab;
  top: 0;
  left: 0; 
  -webkit-transition: all 0.6s ease;
    -moz-transition: all 0.6s ease;
    -o-transition: all 0.6s ease;
    transition: all 0.6s ease;

    box-sizing: border-box;
  -moz-box-sizing: border-box; /*Firefox 1-3*/
  -webkit-box-sizing: border-box; /* Safari */
}

a.magnifier2:hover .zoom{
  opacity: 1
}

a.magnifier2 .zoom{
  opacity: 0;
}