footer {
	display: block;
	font-family: Arial, Helvetica, sans-serif; 
	width: 100%;
}

.main-footer {
	padding-bottom: 31px;
	padding-top: 34px; 
}

.main-footer p {
	color:#7f7f7f;
	float: left;
	font:13px/21px $title-font, Arial, sans-serif;
	margin:0;
}

/* Social Icons */
.soc_icons {
	float: right;
  position: relative;
  display: inline-block;
  white-space: nowrap;
  margin-top: 2px;
}

.soc_icons > li {
  position: relative;
  display: inline-block;
  margin-left: 15px;
}

.soc_icons > li > a {
  position: relative;
  font: normal normal 25px 'FontAwesome';
  line-height: 1em;
  color: #595959;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
}

.soc_icons > li > a:hover {
  color: #fff;
  text-decoration: none;
}

/* Privacy Policy */

.priv_block {
  float: left;
}

.terms_block{
  float: right;
}